.address {
  background-color: var(--light-color);
}
.address .main-title {
  padding-top: 0;
}
.address__filter {
  margin-bottom: 30px;
  display: flex;
}
@media (max-width: 1330px) {
  .address__filter {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .address__filter {
    flex-direction: column;
    margin-bottom: 20px;
  }
}
.address .tabs__nav-item:not(:last-child) {
  margin-bottom: 10px;
}
.address__btn {
  border: none;
  width: 228px;
  height: 50px;
  padding: 0;
  margin-right: 2px;
  color: #A8A9AA;
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  text-align: center;
  background-color: #E4E5E6;
}
@media (max-width: 1330px) {
  .address__btn {
    font-size: 12px;
    width: auto;
    height: 30px;
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .address__btn {
    font-size: 11px;
    height: 20px;
  }
}
.address__tabs .tabs__nav-btn--active {
  z-index: 1;
  color: var(--text-color-dark);
  background-color: #fff;
  position: relative;
}
.address__tabs .tabs__nav-btn--active:after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: var(--secondary-color);
}
@media (max-width: 767px) {
  .address__tabs .tabs__nav-btn--active:after {
    height: 2px;
  }
}
.address__tabs .tabs__panel {
  display: none;
}
.address__tabs .tabs__panel--active {
  display: block;
}
.address__content {
  background-color: #fff;
  display: flex;
  max-height: 400px;
  min-height: 400px;
}
@media (max-width: 1330px) {
  .address__content {
    max-height: 250px;
    min-height: 250px;
  }
}
@media (max-width: 767px) {
  .address__content {
    flex-direction: column-reverse;
    margin: 0 -20px;
    max-height: 100%;
  }
}
.address__info {
  width: 33.3333%;
  min-height: 100%;
  padding: 23px 30px 30px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1330px) {
  .address__info {
    padding: 15px 20px 20px 20px;
  }
}
@media (max-width: 767px) {
  .address__info {
    width: 100%;
  }
}
.address__head {
  position: relative;
  line-height: 140%;
  letter-spacing: 0.02em;
  padding-bottom: 14px;
}
@media (max-width: 1330px) {
  .address__head {
    line-height: 120%;
    padding-bottom: 5px;
  }
}
.address__head:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: var(--secondary-color);
  width: 100px;
  height: 3px;
}
@media (max-width: 1330px) {
  .address__head:after {
    height: 2px;
  }
}
@media (max-width: 767px) {
  .address__head:after {
    height: 1px;
  }
}
.address__title {
  font-weight: 900;
  font-size: 20px;
  text-transform: uppercase;
  color: var(--text-color-dark);
  margin-bottom: 5px;
}
@media (max-width: 1330px) {
  .address__title {
    font-size: 14px;
    letter-spacing: 0.04em;
    margin-bottom: 0;
  }
}
.address__subtitle {
  font-weight: 400;
  font-size: 16px;
}
@media (max-width: 1330px) {
  .address__subtitle {
    font-size: 12px;
  }
}
@media (max-width: 767px) {
  .address__subtitle {
    font-size: 11px;
  }
}
.address__body {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  margin-top: 28px;
}
@media (max-width: 1330px) {
  .address__body {
    margin-top: 19px;
  }
}
@media (max-width: 767px) {
  .address__body {
    margin-bottom: 20px;
    margin-top: 9px;
  }
}
.address__item {
  display: flex;
  align-items: center;
}
.address__item:not(:last-child) {
  margin-bottom: 26px;
}
@media (max-width: 1330px) {
  .address__item:not(:last-child) {
    margin-bottom: 17px;
  }
}
@media (max-width: 767px) {
  .address__item:not(:last-child) {
    font-size: 9px;
    margin-bottom: 9px;
  }
}
.address__icon {
  margin-right: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
@media (max-width: 1330px) {
  .address__icon {
    margin-right: 7px;
  }
}
.address__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: var(--text-color-dark);
}
@media (max-width: 1330px) {
  .address__text {
    font-size: 12px;
  }
}
@media (max-width: 767px) {
  .address__text {
    font-size: 11px;
  }
}
.address__button {
  display: flex;
}
.address__button .btn {
  display: block;
  font-weight: 900;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--text-color-light);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  background-color: var(--secondary-color);
  -webkit-box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  padding: 14px 42px;
}
@media (max-width: 1330px) {
  .address__button .btn {
    font-size: 12px;
    padding: 7px 20px;
  }
}
@media (max-width: 767px) {
  .address__button .btn {
    font-size: 11px;
  }
}
.address__map {
  width: 66.6666%;
  filter: grayscale(1);
}
.address__map iframe {
  border: none;
  height: 100%;
}
@media (max-width: 1330px) {
  .address__map iframe {
    height: 100%;
  }
}
@media (max-width: 767px) {
  .address__map {
    width: 100%;
  }
}

.personnel {
  background-color: var(--light-color);
  padding: 30px 0 60px 0;
}
@media (max-width: 1330px) {
  .personnel {
    padding: 20px 0 40px 0;
  }
}
.personnel__inner {
  padding: 0 60px;
  position: relative;
}
@media (max-width: 1330px) {
  .personnel__inner {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .personnel__inner {
    padding: 0;
  }
}
@media (max-width: 525px) {
  .personnel__inner {
    margin-right: -20px;
  }
}
.personnel .swiper-slide {
  height: auto;
  display: flex;
  flex-direction: column;
}
.personnel__item {
  background-color: #fff;
  padding: 30px 30px 26px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1330px) {
  .personnel__item {
    padding: 20px;
  }
}
@media (max-width: 767px) {
  .personnel__item {
    padding: 10px;
  }
}
.personnel__head {
  display: flex;
  margin-bottom: 18px;
}
@media (max-width: 1330px) {
  .personnel__head {
    margin-bottom: 8px;
  }
}
@media (max-width: 767px) {
  .personnel__head {
    margin-bottom: 7px;
  }
}
.personnel__photo {
  border-radius: 50%;
  border: 2px solid #FFFFFF;
  position: relative;
  z-index: 1;
  max-width: 100px;
  min-width: 100px;
  max-height: 100px;
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
}
@media (max-width: 1330px) {
  .personnel__photo {
    margin-right: 10px;
    max-width: 62px;
    min-width: 62px;
    max-height: 62px;
    min-height: 62px;
  }
}
@media (max-width: 767px) {
  .personnel__photo {
    max-width: 50px;
    min-width: 50px;
    max-height: 50px;
    min-height: 50px;
  }
}
.personnel__photo:before {
  content: "";
  background-color: var(--secondary-color);
  opacity: 0.1;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.personnel__photo picture {
  border: 2px solid #FFFFFF;
  border-radius: 50%;
}
.personnel__photo img, .personnel__photo source {
  width: 82px;
  height: 82px;
  object-fit: cover;
  border-radius: 50%;
}
@media (max-width: 1330px) {
  .personnel__photo img, .personnel__photo source {
    width: 52px;
    height: 52px;
  }
}
@media (max-width: 767px) {
  .personnel__photo img, .personnel__photo source {
    width: 39px;
    height: 39px;
  }
}
.personnel__titles {
  letter-spacing: 0.02em;
  color: var(--text-color-dark);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.personnel__name {
  line-height: 140%;
  letter-spacing: 0.02em;
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 15px;
}
@media (max-width: 1330px) {
  .personnel__name {
    font-size: 14px;
    margin-bottom: 8px;
  }
}
@media (max-width: 767px) {
  .personnel__name {
    font-size: 11px;
  }
}
.personnel__post {
  line-height: 140%;
  letter-spacing: 0.02em;
  font-weight: 400;
  font-size: 12px;
}
@media (max-width: 1330px) {
  .personnel__post {
    font-size: 9px;
    margin-top: -7px;
  }
}
.personnel .address__body {
  margin: 0;
}
.personnel .address__item:not(:last-child) {
  margin-bottom: 16px;
}
@media (max-width: 1330px) {
  .personnel .address__item:not(:last-child) {
    margin-bottom: 9px;
  }
}
.personnel__prev-1, .personnel__prev-2, .personnel__prev-3, .personnel__prev-4 {
  position: absolute;
  left: -5px;
  top: 50%;
  transform: translateY(-50%);
}
.personnel__next-1, .personnel__next-2, .personnel__next-3, .personnel__next-4 {
  position: absolute;
  right: -5px;
  top: 50%;
  transform: translateY(-50%);
}
.personnel__pagination-1, .personnel__pagination-2, .personnel__pagination-3, .personnel__pagination-4 {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
@media (max-width: 1330px) {
  .personnel__pagination-1, .personnel__pagination-2, .personnel__pagination-3, .personnel__pagination-4 {
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .personnel__pagination-1, .personnel__pagination-2, .personnel__pagination-3, .personnel__pagination-4 {
    margin-top: 10px;
  }
}
.personnel .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  background: var(--secondary-color);
  opacity: 0.2;
  border-radius: 0;
  margin: 0 10px;
}
@media (max-width: 1330px) {
  .personnel .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
  }
}
@media (max-width: 767px) {
  .personnel .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
  }
}
.personnel .swiper-pagination-bullet-active {
  opacity: 1;
}
.personnel .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 10px;
}
@media (max-width: 1330px) {
  .personnel .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 5px;
  }
}
@media (max-width: 767px) {
  .personnel .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 2.5px;
  }
}

.questions {
  background-color: var(--light-color);
  padding-top: 0;
}
@media (max-width: 767px) {
  .questions {
    display: none;
  }
}